// There is no easy way to pull in dependencies from a package.json due to how
// Jekyll works. You cannot pull in the sass version when using a cdn since the import
// doesn't get inlined, just referenced.
// Pulling in the css works but we won't be able to extend, add to placeholder selectors,
// or use the breakpoint variables.
@import 'https://cdn.jsdelivr.net/npm/@rolemodel/optics@0.3.1/dist/css/optics.css';

:root {
  --header-height: 6rem;

  --op-color-on-background-alt: var(--op-color-neutral-on-plus-eight-alt);
  --op-letter-spacing-title: 0.2rem;
}

// Misc
@import 'layout';
@import 'utilities';

// Component
@import 'components/button';
@import 'components/list';
@import 'components/pagination';
@import 'components/post';
@import 'components/sidebar';
@import 'components/syntax-highlighting';
@import 'components/table-of-contents';
